import React from "react";
import { Button, Card, OverlayTrigger, Popover, Stack } from "react-bootstrap";
import {
  Share,
  Download,
  Clipboard,
  ClipboardCheck,
} from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import styles from "./style.module.css";

function ZineCard(props) {
  const navigate = useNavigate();
  const [copyLinkButtonClicked, setCopyLinkButtonClicked] =
    React.useState(false);
  const { zine } = props;
  return (
    <Card className={styles.card}>
      <Card.Img
        onClick={() => {
          navigate(`/zines/${zine.path}`);
        }}
        variant="top"
        src={zine.thumbnail.default}
        className={styles.cardImage}
      />
      <Card.Body className={styles.cardBody}>
        <Card.Title className={styles.cardTitle}>{zine.title}</Card.Title>
        <Stack
          gap={3}
          direction="horizontal"
          className={styles.cardButtonStack}
        >
          <a href={zine.file} download={`${zine.path}.pdf`}>
            <div className={styles.roundedContainer}>
              <Download className={styles.buttonIcon} />
            </div>
          </a>
          <OverlayTrigger
            trigger="click"
            placement="bottom"
            onExit={() => setCopyLinkButtonClicked(false)}
            overlay={
              <Popover className={styles.linkPopover}>
                <Popover.Header as="h3">Copy Link</Popover.Header>
                <Popover.Body>
                  <Stack direction="horizontal">
                    <span
                      className={styles.linkPopoverText}
                    >{`${window.location.href}/zines/${zine.path}`}</span>
                    {!copyLinkButtonClicked ? (
                      <Clipboard
                        className={styles.copyToClipboardButton}
                        size={25}
                        onClick={() => {
                          navigator.clipboard.writeText(
                            `${window.location.href}/zines/${zine.path}`
                          );
                          setCopyLinkButtonClicked(true);
                        }}
                      />
                    ) : (
                      <ClipboardCheck
                        className={styles.copyToClipboardButton}
                        size={25}
                      />
                    )}
                  </Stack>
                </Popover.Body>
              </Popover>
            }
          >
            <div className={styles.roundedContainer}>
              <Share
                className={`${styles.buttonIcon} ${styles.linkIcon}`}
                size={20}
              />
            </div>
          </OverlayTrigger>

          <Button
            className={styles.button}
            onClick={() => {
              navigate(`/zines/${zine.path}`);
            }}
          >
            Open
          </Button>
        </Stack>
      </Card.Body>
    </Card>
  );
}
export default ZineCard;
